import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import linkState from 'linkstate';
import { route } from 'preact-router';

const initialState = {
    newTrack: {},
};

@observer
class Input extends Component {
    render() {
        const { saved, track, field, title, help, onInput } = this.props;
        const value = track[field];
        return (
            <div class={`form-group ${saved[`${field}.${track.id}`] ? 'is-valid' : ''}`}>
                <label for={`${field}Input`}>{title}</label>
                <input type='text' class={`form-control ${saved[`${field}.${track.id}`] ? 'is-valid' : ''}`} id={`${field}Input`} aria-describedby={`${field}Help`}
                    value={value}
                    data-id={track.id}
                    data-field={field}
                    onInput={onInput}
                />
                {saved[`${field}.${track.id}`] ? <div class={`valid-feedback`} style='display: block;'>Endringene er lagret</div> : 
                    <small id={`${field}Help`} class='form-text text-muted'>{help}</small>}
            </div>
        );
    }
}

@observer
class InputNew extends Component {
    onInput = (e) => {
        const { field } = this.props;
        const value = e.target.value;
        const { trackStore } = this.props.stores;
        console.log('trackStore.newTrack', trackStore.newTrack);
        trackStore.updateObjectKeyValue('newTrack', field, value);
    }

    render() {
        const { field, title, help } = this.props;
        const { trackStore } = this.props.stores;
        const { newTrack } = trackStore;
        return (
            <div class='form-group'>
                <label for={`${field}Input`}>{title}</label>
                <input type='text' class={`form-control`} id={`${field}Input`} aria-describedby={`${field}Help`}
                    onInput={this.onInput}
                    value={newTrack[field]}
                />
                <small id={`${field}Help`} class='form-text text-muted'>{help}</small>
            </div>
        );
    }
}

@observer
class TracksEdit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.updateTimer = {};
    }

    async loadTrack() {
		const { trackStore } = this.props.stores;
        if (this.props.track === 'new') {
            this.setState({ createNew: true });
        } else {
            trackStore.load(this.props.track);
        }
    }

	resetTrack() {
        const { trackStore } = this.props.stores;
        trackStore.updateKeyValue('newTrack', {});
	}

	insertTrack = async () => {
		const { trackStore } = this.props.stores;
		const { newTrack } = trackStore;
		await trackStore.insert({ ...newTrack });
        this.resetTrack();
        route(`/tracks/`, true);
	}

    updateField = (e) => {
        const { id, field } = e.target.dataset;
        const value = e.target.value;
        const { trackStore } = this.props.stores;

        trackStore.updateField(id, field, value);

        clearTimeout(this.updateTimer[field]);
        this.updateTimer[field] = setTimeout(async () => {
            this.doUpdateField(id, field, value);
        }, 500);
    }

    async doUpdateField(id, field, value) {
        const { trackStore } = this.props.stores;
        await trackStore.saveField(id, field, value);
    }

    back() {
        route('/tracks/', true);
    }

    componentWillMount() {
        this.loadTrack();
    }

	render() {
        const { createNew } = this.state;
		const { trackStore } = this.props.stores;
		const { insertStatus, saved, track } = trackStore;
        const { name } = track;
		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 60px;'>
                <button class='btn btn-link float-left' onClick={this.back}>
                    <i class='fas fa-arrow-left' />
                </button>
				<div class='row'>
					<div class='offset-0 col-12 offset-sm-2 col-sm-8 offset-lg-4 col-lg-4'>
                        {createNew ? (
                            <div> 
                                <h5>Legg til ny treningsrunde</h5>
                                <InputNew stores={this.props.stores} field='name' title='Tittel' help='Tittel på denne turen. "Fin høsttur i øsende regn..."' />
                                <InputNew stores={this.props.stores} field='distanceKm' title='Distanse i km' help='Hvor lang var turen i km.' />
                                <InputNew stores={this.props.stores} field='elevation' title='Høydemeter' help='Høydemeter du klatret på denne turen.' />

                                <button type='button' class='btn btn-primary'
                                    onClick={this.insertTrack}
                                >
                                    Registrer ny treningsrunde
                                </button>
                            </div>
                        ) : (
                            <div>
                                <h5>Endre {name}</h5>
                                <Input saved={saved} track={track} field='name' title='Tittel' help='Tittel på denne turen. "Fin høsttur i øsende regn..."' onInput={this.updateField} />
                                <Input saved={saved} track={track} field='distanceKm' title='Distanse i km' help='Hvor lang var turen i km.' onInput={this.updateField} />
                                <Input saved={saved} track={track} field='elevation' title='Høydemeter' help='Høydemeter du klatret på denne turen.' onInput={this.updateField} />
                            </div>                                                        
                        )}
					</div>

					{insertStatus && <div class='alert alert-success mt-4 col-12' role='alert'>
						Runden er lagret.
					</div>}
				</div>
			</div>
		);
	}
}

export default TracksEdit;
